
#article-write-comment .atc-title {
  font-size: 24px;
  line-height: 1.4;
  padding: 10px 20px;
}
#article-write-comment .write {
  background: #fff;
  padding: 10px 20px;
}
#article-write-comment .write textarea {
    border: none;
    resize: none;
    height: 60px;
    font-size: 14px;
    line-height: 20px;
    width: 100%;
    display: block;
}
#article-write-comment .send-btn {
  padding: 20px;
}
#article-write-comment .send-btn button {
    border: none;
    background: #09BB07;
    color: #fff;
    font-size: 18px;
    line-height: 36px;
    height: 36px;
    text-align: center;
    border: none;
    border-radius: 5px;
    width: 100%;
    display: block;
}
#article-write-comment .send-btn button.gray {
      color: #e5e5e5;
}



