
@charset "UTF-8";
* {
  margin: 0;
  padding: 0;
  background: transparent;
  -webkit-tap-highlight-color: transparent;
  -webkit-font-smoothing: antialiased;
  outline: none;
  -webkit-appearance: none;
}
h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
  font-weight: 400;
}
html, body, div, span, ul, li, ol, dl, dd, dt, form, h1, h2, h3, h4, h5, h6, p, form, a, em, i, s {
  padding: 0;
  margin: 0;
  list-style: none;
  border: 0;
}
i, em, var, h4, i, s, h2 {
  font-style: normal;
  font-weight: normal;
}
body {
  font: 16px "宋体,tahoma", "\5FAE\8F6F\96C5\9ED1";
  color: #333;
  width: 100%;
  -webkit-text-size-adjust: none;
}
input[type="reset"]::-moz-focus-inner,
input[type="button"]::-moz-focus-inner,
input[type="submit"]::-moz-focus-inner,
input[type="file"] > input[type="button"]::-moz-focus-inner {
  border: none;
  padding: 0;
}
::-webkit-input-placeholder {
  color: #999;
}
a {
  text-decoration: none;
  cursor: pointer;
  outline: 0;
  display: inline;
  padding: 0;
}
a img {
  border: 0;
}



