
body {
  background-color: #f3f3f3;
}
#article-detail .atc-container {
  background-color: #fff;
  padding: 20px 15px 15px 15px;
}
#article-detail .atc-title {
  font-size: 24px;
  line-height: 1.4;
  display: block;
  word-wrap: break-word;
  margin-bottom: 10px;
}
#article-detail .tm-con {
  font-size: 0;
  line-height: 1;
  margin-bottom: 10px;
}
#article-detail .tm-con .g-tx {
    display: inline-block;
    font-size: 16px;
    margin-right: 8px;
    margin-bottom: 10px;
    color: #999;
}
#article-detail .tm-con .l-tx {
    display: inline-block;
    color: #1062a9;
    font-size: 16px;
    margin-right: 8px;
    margin-bottom: 10px;
}
#article-detail .rich-content {
  line-height: 2;
  word-wrap: break-word;
}
#article-detail .rich-content img, #article-detail .rich-content video, #article-detail .rich-content object, #article-detail .rich-content embad {
    max-width: 100%;
    display: block;
    margin: 0 auto;
}
#article-detail .atc-data {
  margin-top: 10px;
  color: #999;
  font-size: 0;
  line-height: 1;
}
#article-detail .atc-data .g-tx {
    font-size: 16px;
    margin-right: 16px;
    display: inline-block;
}
#article-detail .atc-data em {
    vertical-align: text-top;
}
#article-detail .atc-data .t-su {
    display: inline-block;
    float: right;
    font-size: 16px;
}
#article-detail .atc-data:after {
    display: block;
    clear: both;
    content: " ";
    height: 0;
    visibility: hidden;
    width: 100%;
}
#article-detail .commnet {
  padding: 20px 15px 15px 15px;
}
#article-detail .commnet .t-title {
    position: relative;
}
#article-detail .commnet .t-title .span {
      background-color: #f3f3f3;
      width: 100px;
      height: 40px;
      line-height: 40px;
      font-size: 16px;
      color: #666;
      text-align: center;
      margin: 0px auto;
      position: relative;
      z-index: 2;
}
#article-detail .commnet .t-title .line {
      border-top: dashed 1px #999;
      height: 1px;
      position: absolute;
      width: 100%;
      z-index: 1;
      top: 20px;
}
#article-detail .commnet .w-comment {
    text-align: right;
    color: #1062a9;
    font-size: 16px;
    line-height: 1.4;
}
#article-detail .commnet .w-comment a:link {
      color: #1062a9;
}
#article-detail .commnet .w-comment a:active {
      color: #1062a9;
}
#article-detail .commnet .w-comment a:visited {
      color: #1062a9;
}
#article-detail .commnet .com-item {
    display: -webkit-box;
    padding: 10px 0;
}
#article-detail .commnet .com-item .us-con {
      width: 40px;
      height: 40px;
      display: block;
      overflow: hidden;
}
#article-detail .commnet .com-item .us-con img {
        width: 100%;
}
#article-detail .commnet .com-item .msg {
      padding-left: 10px;
      -webkit-box-flex: 1;
}
#article-detail .commnet .com-item .msg .m-top {
        height: 24px;
}
#article-detail .commnet .com-item .msg .m-top u-name {
          font-size: 14px;
          line-height: 24px;
}
#article-detail .commnet .com-item .msg .m-con {
        font-size: 16px;
        word-wrap: break-word;
        line-height: 22px;
}
#article-detail .commnet .com-item .msg .m-btm {
        margin-top: 8px;
        font-size: 12px;
        color: #999;
}
#article-detail .load {
  font-size: 14px;
  text-align: center;
  line-height: 20px;
}



