
#article-list .list {
  padding: 15px 7px 7px 7px;
}
#article-list .list a:link {
    color: #333;
}
#article-list .list a:active {
    color: #333;
}
#article-list .list a:visited {
    color: #333;
}
#article-list .list .li1 {
    display: block;
    padding-bottom: 7px;
    padding-top: 7px;
    margin-top: 60px;
    background-color: #fff;
    position: relative;
}
#article-list .list .li1 .time {
      position: absolute;
      width: 100%;
      font-size: 12px;
      height: 20px;
      line-height: 20px;
      text-align: center;
      top: -40px;
}
#article-list .list .li1 .time .dd {
        padding: 0 6px;
        color: #fff;
        background-color: rgba(0, 0, 0, 0.4);
        border-radius: 5px;
        display: inline-block;
}
#article-list .list .li1 .img-box {
      width: 100%;
      min-height: 30px;
      display: block;
      position: relative;
      overflow: hidden;
}
#article-list .list .li1 .img-box img {
        width: 100%;
        display: block;
}
#article-list .list .li1 .img-box .title {
        background-color: rgba(0, 0, 0, 0.6);
        height: 30px;
        line-height: 30px;
        padding: 0 8px;
        color: #fff;
        font-size: 18px;
        position: absolute;
        left: 0;
        bottom: 0;
        overflow: hidden;
        width: 100%;
}
#article-list .list .li2 {
    background-color: #fff;
    display: block;
    padding: 8px 0;
    border-top: solid 1px #e5e5e5;
    width: 100%;
}
#article-list .list .li2 table {
      width: 100%;
}
#article-list .list .li2 table td:last-child {
      width: 50px;
}
#article-list .list .li2 .title {
      font-size: 16px;
      line-height: 20px;
      padding-right: 15px;
      overflow: hidden;
}
#article-list .list .li2 .img-box {
      display: block;
      width: 50px;
      overflow: hidden;
}
#article-list .list .li2 .img-box img {
        width: 100%;
        display: block;
}
#article-list .load {
  font-size: 14px;
  text-align: center;
  line-height: 20px;
  margin-top: 10px;
}



